.container {
    display:flex;
    width:100%;
    height:100%;
    justify-content:center;
}

.portfolio-container {
    display:flex;
    align-items: center;
    justify-content: center;
    width:50vw;
    height:100vh;
}

.portfolio {
    overflow-y:scroll;
    height:80vh;
    font-family:'Roboto';
    font-size:1.5vw;
    width:40vw;
    letter-spacing: 1px;
    text-align:left;
    padding-right:5vw;
    padding-left:2vw;

}

.dribbble {
    padding-left:10vw;
    padding-top:3vh;
}

@media screen and (max-width: 768px) {
    .portfolio {
        padding:0;
        width:70vw;
        font-size:4vw;
        height:70vh;
        padding-top:50vh;
    }

    .portfolio-container {
        width:90vw;
    }

    .dribbble {
        width:30vw;
        padding:0;
    }
}