.container {
    display:flex;
    width:100%;
    height:100%;
    justify-content:center;
}

.bio-container {
    display:flex;
    align-items: center;
    justify-content: center;
    width:50vw;
    height:100vh;
}

.bio {
    overflow-y:scroll;
    height:50vh;
    font-family:'Roboto';
    font-size:1.5vw;
    width:40vw;
    letter-spacing: 1px;
    text-align:left;
    padding-right:5vw;
    padding-left:2vw;

}

@media screen and (max-width: 768px) {
    .bio {
        padding:0;
        width:70vw;
        font-size:4vw;
        height:70vh;
    }

    .bio-container {
        width:90vw;
    }
}

